<!-- 搜索页 -->
<template>
  <div class="search">
    <SearchBar @searchVal="searchVal" @keyboardSearch="keyboardSearch" >
      <!-- <div class="search-btn" @click="searchHandle">搜索</div> -->
    </SearchBar>
    <div class="content">
      <div class="search-list" v-show="inputVal.length > 0">
        <div class="loading" v-show="isLoadingShow">
          <img src="../../assets/images/loading.gif" alt />
          <p>搜索中...</p>
        </div>
        <div class="empty" v-show="isEmptyShow && searchViewResultList.length === 0">搜索结果为空</div>
        <div class="search-result" v-show="!isLoadingShow">
          <div class="view" v-show="searchViewResultList.length">
            <h5><i />相关视图</h5>
            <ul>
              <li v-for="(item, index) in searchViewResultList" :key="index" @click="toLink(searchViewResultList[index], 'view')">
                <span class="left">
                  <span class="text" v-html="item.text"></span>
                  <img v-show="item.type !== 'keyWord'" src="../../assets/images/icon/icon_jyzl_shand.png" alt="">
                </span>
                <span class="auth">
                  <span v-show="item.type === 'keyWord'" class="key-icon">关键字</span>
                  <!-- <span v-show="item.type === 'keyWord'">&#8594;&nbsp;{{item.modulename}}</span> -->
                  <span v-show="item.quanxianC === 0">&nbsp;{无权限}</span>
                </span>
              </li>
            </ul>
            <ul v-show="searchMoreViewResultList.length && isShowMore">
              <li v-for="(item, index) in searchMoreViewResultList" :key="index" @click="toLink(searchMoreViewResultList[index], 'view')">
                <span class="left">
                  <span class="text" v-html="item.text"></span>
                  <img v-show="item.type !== 'keyWord'" src="../../assets/images/icon/icon_jyzl_shand.png" alt="">
                </span>
                <span class="auth">
                  <span v-show="item.type === 'keyWord'" class="key-icon">关键字</span>
                  <span v-show="item.quanxianC === 0">&nbsp;{无权限}</span>
                </span>
              </li>
            </ul>
          </div>
          <div class="more" @click="showMore" v-if="searchMoreViewResultList.length">
            {{isShowMore ? '收起' : '更多'}}
            <img :class="[isShowMore ? 'roate' : '']" src="../../assets/images/icon_show@2x.png" alt="">
          </div>
          <div class="article" v-show="searchArticleResultList.length">
            <h5><i />相关文章</h5>
            <ul>
              <li v-for="(item, index) in searchArticleResultList" :key="index" @click="toLink(searchArticleResultList[index], 'article')">
                <span class="left">
                  <span class="text" v-html="item.text"></span>
                </span>
                <span class="auth">
                  <span v-show="item.type === 'keyWord'" class="key-icon">关键字</span>
                </span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
import SearchBar from "../../components/SearchBar";
export default {
  name: "Search",
  components: {
    SearchBar
  },
  data() {
    return {
      inputVal: "",
      applyVal: '',
      isLoadingShow: false,
      isEmptyShow: false,
      isShowMore: false,
      searchViewResultList: [],
      searchMoreViewResultList: [],
      searchArticleResultList: [],
    };
  },
  created() {},
  methods: {
    // 获取搜索框值
    searchVal(val) {
      this.inputVal = val;
      this.searchViewResultList = [];
      if (this.inputVal) {
        this.isLoadingShow = true;
        this.isEmptyShow = false;
        const reg = new RegExp(this.inputVal, "g");
        this.$axios.post('/hopeModule/searchModuleByName', {
          "name": this.inputVal,
          "aamid": "010203",
          "deptid": "111213",
          "odeptid": "0010101032"
        }).then(res => {
        	this.isLoadingShow = false;
        	if (res.status === 200 && res.data.status === '000000') {
            const resData = res.data.data || [];
        		const viewKeyList = resData.module.list[1].styleList || [];
            const viewTableList = resData.module.list[0].styleList || [];
            const articleKeyList = resData.article.list[1].styleList || [];
            const articleTableList = resData.article.list[0].styleList || [];
            viewKeyList.forEach(item => {
              item.type = 'keyWord';
            });
            articleKeyList.forEach(item => {
              item.type = 'keyWord';
            });
            const showViewList = viewTableList.slice(0, 2).concat(viewKeyList.slice(0, 2));
            const articleList = articleTableList.concat(articleKeyList);
            const moreViewList = viewKeyList.slice(2).concat(viewTableList.slice(2));

            if (viewKeyList.length === 0 && viewTableList.length === 0 && articleKeyList.length === 0 && articleTableList.length === 0) {
              this.isEmptyShow = true;
              return;
            }
            // 关键字高亮
            this.searchViewResultList = showViewList.map(i => {
              return {
                text: i.type && i.type === 'keyWord' ? i.keyname.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ) : i.modulename.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ),
                ...i
              };
            });
            this.searchMoreViewResultList = moreViewList.map(i => {
              return {
                text: i.type && i.type === 'keyWord' ? i.keyname.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ) : i.modulename.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ),
                ...i
              };
            });
            this.searchArticleResultList = articleList.map(i => {
              return {
                text: i.textname.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ),
                ...i
              };
            });
        	} else {
        		this.isEmptyShow = true;
        	}
        }).catch(err => {
        	this.isLoadingShow = false;
        	this.isEmptyShow = true;
        });
      }
    },
    // 点击展示更多
    showMore() {
      this.isShowMore = !this.isShowMore;
    },
    // 键盘搜索事件
    keyboardSearch() {

    },
    // 跳转到详情
    toLink(item, type) {

    },
  }
};
</script>
<style lang="less" scoped>
.search {
  min-height: 100%;
  background: #fff;
}
.search-btn{
  width: 0.52rem;
  height: 0.3rem;
  background: #45A4B1;
  border-radius: 0.15rem;
  color: #fff;
  text-align: center;
  line-height: 0.3rem;
  font-size: 0.15rem;
  margin-left: 0.06rem;
}
.content {
  .search-list {
    .loading {
      padding-top: 1.2rem;
      text-align: center;
      font-size: 0.14rem;
      color: #666666;
      img {
        width: 0.4rem;
      }
    }
    .empty {
      padding-top: 1.2rem;
      text-align: center;
      font-size: 0.14rem;
      color: #666666;
    }
    li {
      height: 0.44rem;
      line-height: 0.44rem;
      font-size: 0.15rem;
      color: #232323;
      border-bottom: 1px solid #eaeaea;
      box-sizing: border-box;
      padding: 0 0.1rem 0 0.17rem;
      display: flex;
      align-items: center;
      position: relative;
      overflow: hidden;
      .left{
        flex: 1;
        display: flex;
        align-items: center;
        overflow: hidden;
        height: 0.44rem;
        .text{
          height: 0.44rem;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
          // max-width: calc(100% - 0.6rem);
          padding-right: 0.09rem;
          box-sizing: border-box;
        }
      }
      img{
        width: 0.3rem;
        height: 0.3rem;
        margin-left: 0.1rem;
      }
      .auth{
        float: right;
        font-size: 0.12rem;
        color: #969696;
      }
      .key-icon{
        display: inline-block;
        width: 0.5rem;
        height: 0.16rem;
        border: 1px solid #45A4B1;
        border-radius: 0.08rem;
        font-size: 0.12rem;
        color: #45A4B1;
        line-height: 0.16rem;
        text-align: center;
        white-space: nowrap;
      }
    }
  }
  .search-result{
    h5{
      height: 0.4rem;
      font-size: 0.14rem;
      color: #969696;
      line-height: 0.4rem;
      position: relative;
      text-indent: 0.16rem;
      border-bottom: 1px solid #EAEAEA;
      i{
        position: absolute;
        width: 0.04rem;
        height: 0.24rem;
        background: #2A7875;
        left: 0;
        top: 0.08rem;
      }
    }
  }
  .more{
    height: 0.16rem;
    padding-top: 0.1rem;
    font-size: 0.11rem;
    color: #45A4B1;
    display: flex;
    justify-content: center;
    align-items: center;
    img{
      width: 0.12rem;
      height: 0.13rem;
      margin-left: 0.02rem;
    }
    .roate{
      transform: rotate(180deg);
    }
  }
}
</style>